.react-duration-input[type=text] {
  display: inline-flex;
  vertical-align: middle;
  border: 1px solid #999999;
  border-radius: 4px;
  font-variant-numeric: tabular-nums;
  letter-spacing: 1.5px;
  line-height: normal;
  padding: 0px 0px 0px 0.3em;
  background: transparent;
  box-shadow: 0 0 2px 2px transparent;
  outline: 2px solid transparent;
  transition: border-color 0.25s ease-in-out, box-shadow 0.25s ease-in-out;
  height: 1.6em;
}
.react-duration-input[type=text][data-format=milliseconds] {
  width: 7.66em;
}
.react-duration-input[type=text][data-format=seconds] {
  width: 5em;
}
.react-duration-input[type=text]:disabled {
  box-shadow: 0 0 2px 2px #bbbbbb;
  color: gray;
  cursor: not-allowed;
  user-select: none;
}
.react-duration-input[type=text]:focus, .react-duration-input[type=text]:focus-visible, .react-duration-input[type=text]:active {
  box-shadow: 0 0 2px 2px rgb(0, 95, 204);
  border: 1px solid transparent;
}
.react-duration-input[type=text]:focus:invalid, .react-duration-input[type=text]:focus-visible:invalid, .react-duration-input[type=text]:active:invalid {
  box-shadow: 0 0 2px 2px #ea404d;
}
.react-duration-input[type=text]:invalid {
  box-shadow: 0 0 2px 2px #ea404d;
  border: 1px solid transparent;
}

